.topbar {
    width: 100%;
    height: 50px;
    background: white;
}

.topbarwrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    color: darkblue;
    cursor: pointer;
}

.topRight {
    display: flex;
    align-items: center;
} 
.topbarIconsContainer {
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color: #555;

}
.topIconBadge {
    width: 15px;
    height: 15px;
    position: absolute;
    top: -10px;
    right: -5px;
    background: red;
    color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
}

.topAvatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}